body,
main,
div#job-requisitions-module {
	margin: auto;
}

body {
	--test-border-color: #ffcc86;
	--test-border-type: solid;
	--test-border: 2px var(--test-border-type) var(--test-border-color);
	--white: #f8f8f8;
	--button-color: var(--white);
	--blue-highlight: rgb(39, 39, 164);
	--blue-highlight: #1a3056; /* Heipro dark navy -- not 40, 80, 120 as in our brand guide */
	--toggle-bkg: var(--blue-highlight);
	--brand-font: 'Roboto Condensed',
	--font-family: var(--brand-font), verdana, monospace;
	--link-hover: #1a3056;
	--button-font-size: 14px;
	--yellow: rgb(255, 198, 0);
}

.top-job-requisitions {
	display: grid;
	place-items: center;
	text-transform: uppercase;
	letter-spacing: 1em;

	& h4 {
		font-size: 1.9ch;
		letter-spacing: 0.3ch;
	}
}

#job-requisitions-module {
	display: flex;
	flex-flow: column nowrap;
	align-items: stretch;
	position: relative;
	width: 100%;
	padding-top: 10px;
	margin-top: 22px;

	border-bottom: 14px solid var(--yellow);

	@media (--wide) {
		flex-flow: row nowrap;
	}

}

/* ================= Select Element Container & Children Styles ================= */
#job-requisitions-select-element-container {
	display: flex;
	flex-flow: column nowrap;

	/* background: linear-gradient(to bottom, #285079 1%, #0273b9 10%, #0273b9 80%, #285079); */
	background-color: #1a3056;
	min-height: 240px;
	width: 100%;
	position: relative;
	padding-bottom: 30px;
	padding-top: 30px;
	margin-bottom: 2.2ch;

	@media (--wide) {
		height: 100vh;
		width: 240px;
		position: sticky;
		top: 5px;
		left: 0;
	}
}

.job-requisitions-select-element {
	width: 100%;
	display: flex;
	flex-flow: row nowrap;

	@media (--wide ) {
		flex-flow: column nowrap;
		justify-content: flex-start;
	}

	& > .select-cities-top-container {
		display: none;

		@media (--wide) {
			display: none;
		}
	}

	& > .select-cities-bottom-container {
		width: 100%;
		display: flex;
		flex-flow: column nowrap;
		justify-content: center;
		align-items: center;
		margin-top: 16px;
		padding-top: 2vw;

		& .select-cities-bottom-button-container {
			margin: 15px 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
		}
	}
}

/* City names in a job requisitions popover are highlighted when the corresponding input is checked*/
.highlight-this-city {
	color: var(--yellow);
	order: -1;
}


button.select-cities-bottom-button {
	color: var(--button-color);
	border: 2px solid var(--button-color);
	font-size: var(--button-font-size);
	font-weight: 700;
	text-transform: uppercase;
	min-width: 180px;
	transform: scale(0.8);
	position: relative;
	padding: 10px;
	letter-spacing: 2px;
	background: var(--toggle-bkg);
	transition: all 1.9s ease;

	&:disabled {
		background: transparent;
		border-color: transparent;
	}

	& > div {
		display: flex;
	}
}

/* Container for the checkboxes used to narrow down the job listings by city */
.cities-select-container {
	width: 94vw;
	display: flex;
	flex-direction: column;
	max-height: 220px;
	flex-wrap: wrap;
	padding-left: 3vw;

	@media (--narrow) {
		width: 100%;
		max-height: 390px;
	}

	@media (--wide) {
		padding-left: 14px;
		width: 100%;
		max-height: unset;
	}

}

/* Single selector checkbox container - a span that contains checkbox where you can narrow down the job listings by city  and label */
.city-select-container {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-items: flex-end;
	align-content: flex-start;
	position: relative;
	overflow: hidden;
	color: #efefef;
	min-height: 28px;
	border-color: rgba(0 0 0/0.04);
	border-left: 1px solid rgba(0 0 0/0.04);
	margin-left: 2px;
	width: calc(94vw / 4.4);
	padding-left: 3px;
	transition: all 0.6s ease;

	@media (--narrow) {
		width: 44vw;
	}

	@media (--wide) {
		width: 100%;
		min-height: 30px;
	}

	/* slides in behind the container when the input is checked */
	&::before {
		position: absolute;
		top: 0;
		left: 0;
		content: '';
		height: 100%;
		width: 80%;
		min-width: 120px;
		min-height: 100%;
		background: var(--frosty);
		border-bottom: 2px solid rgba(0, 0, 0, 0.15);
		border-left: 2px solid rgba(255, 255, 255, 0.25);
		transform: translateX(-102%);
		transition: all 0.6s cubic-bezier(0.1, 1.15, 0.84, 1.04);
	}

	&.inputChecked {

		&::before {
			top: -2px;
			transform: translateX(0);
			transition: all 0.6s cubic-bezier(0.1, 1.15, 0.84, 1.04);
		}
	}

	& label.city-select-label {
		display: unset;
		margin-bottom: unset;
		position: absolute;
		left: 26px;
		bottom: 1px;
		font-size: 16px;
		font-weight: 300;
		letter-spacing: 1px;


		@media (--wide) {
			font-size: 18px;
		}
	}

	& > input {
		position: absolute;
		left: 6px;
		bottom: 4px;
		accent-color: #ffc600;
		margin: 0;
		align-self: center;

		&:checked + label {
			color: #ffc600;
		}

	}
}

/* ================= Job Requisitions Container ================= */
#job-requisitions-all-requisitions-container {
	--job-requisitions-width: 100%;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
	align-items: flex-start;
	height: auto;
	width: var(--job-requisitions-width);
	margin: 0 auto;
	gap: 15px 10px;
	padding-left: 20px;

	@media (--wide) {
		--job-requisitions-width: calc(100% - 240px);
		justify-content: flex-start;
		min-height: 110vh;

	}

	@media (--narrow) {
		padding-left: 0;
		flex-flow: column nowrap;
		justify-content: space-around;
		align-items: stretch;
		gap: 15px 0;

	}
}

/* Tooltip shows up when hovering over the "i" and displays all the cities the job has listed as location */
.city-tooltip-container {
	--height-weight-line: 1.75rem;
	display: inline-block;
	background-color: rgb(40, 80, 120);
	border-radius: 16px;
	color: #efefef;
	cursor: default;
	font-weight: 700;
	position: relative;
	text-align: center;
	line-height: 30px;
	height: 30px;
	width: 30px;
	transition: transform 0.4s ease-in-out;

	&:hover {
		transform: scale(1.05);
		transition: transform 0.4s ease-in-out;
	}

	/* This is the TOOLTIP */
	&:hover .popover {
		display: flex;
		flex-flow: column wrap;
		flex-flow: row wrap;
		justify-content: dlex-start;
		align-items: flex-start;
		align-content: flex-start;
		animation: fade-in 0.3s linear 1, move-up 0.5s linear 1;
	}

	& .popover {
		font-weight: 400;
		--popover-background-color: rgba(2, 0, 0, 0.65);
		--popover-width: 300px;
		background-color: rgba(2, 0, 0, 0.65);
		width: var(--popover-width);
		display: none;
		position: absolute;
		bottom: 42px;
		color: inherit;
		border-radius: 5px;
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
		left: -195px;
		z-index: 1;
		padding: 8px 6px;

		/* For items with over 20 cities attached */
		&.wider {
			color: inherit;
		}

		& > span {
			margin-left: 1px;
			display: inline-block;
			text-align: left;

			&.city-tag {
				line-height: 1.5;
				font-size: 12px;
				letter-spacing: 2px;
				width: 49%;

				/* Probably better to only show the first 30 cities -- there are some edge cases that cause the popover to have hidden cities anyway*/
				&:nth-of-type(n+31) {
					display: none;
				}
			}
		}

		/* This is the pointer part of the popover/tooltip */
		&::before {
			display: block;
			content: '';
			position: absolute;
			bottom: -7px;
			left: 70%;
			margin-left: -7px;
			border-top: 7px solid rgba(2, 0, 0, 0.65);
			border-right: 7px solid transparent;
			border-left: 7px solid transparent;
		}
	}
}

@supports (backdrop-filter: blur(10px)) {
	.city-tooltip-container .popover {
		backdrop-filter: blur(10px);
	}
}

/* SINGLE JOB REQUISITION CARD */
.single-job {
	writing-mode: horizontal-tb;
	display: flex;
	flex-flow: column nowrap;
	flex-flow: row nowrap;
	width: 96vw;
	justify-content: center;
	align-items: stretch;
	box-shadow: 3px 4px 6px 2px rgba(0 0 0/0.15);
	background: #f5f5f5;
	margin-left: 1vw;

	@media (--wider-than-narrow) {
		width: max(350px, 24vw);
		min-height: 440px;
	}

	& h3 {
		line-height: 0.8;
	}

	/* Added to single job requisitions when the data-cities element does NOT have any of the checked cities in it */
	&.doNotDisplay { display: none; }

	& a {
		text-decoration: none;
		color: var(--blue-highlight);
	}

	& > div,
	& > figure {
		transition: all 0.6s ease-in-out;
	}

	& div {
		display: flex;
		flex-wrap: nowrap;
		flex-direction: column;

		& > div {
			flex-direction: row;
			justify-content: flex-end;
			align-items: center;
		}
	}

	& > figure {
		margin-block-end: 6px;
		margin-block-start: 0;
		width: 35vw;

		@media (--wider-than-narrow) {
			display: none;
		}
	}

	& .job-information {
		padding: 1em 0.5em;
		max-width: unset;
		min-height: 100%;
		justify-content: space-around;
		align-items: stretch;
		flex: 3 3 auto;

		/* No padding needed on items that don't have the justify-content set to 'flex-end' */
		& > div:not(.jobphoto, .joblocations) {
			padding-right: 2px;
		}
	}

	& .joblocations {
		justify-content: center;
		align-items: center;

		& > h3 {
			margin-block-end: 0;
			margin-block-start: 0;
			margin-right: 1vw;
			line-height: unset;

			/* stylelint-disable */
			font-weight: unset;
			/* stylelint-enable */
		}
	}
}

/* on screens larger than 740px only -- otherwise the figure shows have to keep the 'div' in here otherwise the user agent stylesheet overrides the display property */
div.jobphoto {
	flex: unset;
	min-height: 50%;
	min-width: 100%;
	background-size: cover;
	background-repeat: no-repeat;

	@media (--narrow) {
		display: none;
	}

	@media (--wider-than-narrow) {
		display: flex;
	}
}


.single-job .job-information {

	& div.joblink,
	div.jobposted {
		align-self: flex-end;
	}

	& > div.jobtitle {
		padding-right: unset;
		align-self: center;
		padding-block-start: 8px;
		padding-block-end: 4px;
		padding-inline-start: 1em;
		padding-inline-end: 4px;
		writing-mode: horizontal-tb;

	}
}

div.joblink > span {
	background: #e8e8e8;
	margin-bottom: 0;
	padding: 1.6ch;
	align-self: flex-end;
	border-radius: 15px;
	margin-top: 1.15ch;
}
