/* stylelint-disable */
:root {
	--color-dark: #222221;
	--color-light: rgba(0, 0, 0, 0.3);
	--module-size: 1.5em;
	/* Scoll the body? Normally, yes. Widescreens, no */
	--body-overflow: initial;

	/* LIGHT THEME */
	--foreground: #f8f8f8; /* nearly white */
	--background: #111; /* nearly black */
	--frosty: rgba(255, 255, 255, 0.3);
	--frosted: rgba(255, 255, 255, 0.3);
	--dark-frosty: rgba(0, 0, 0, 0.3);
	--dark-frosted: rgba(0, 0, 0, 0.3);
	--standard-border: 1px solid var(--frosted);

	/* FONT RELATED */
	/*#region*/

	/* FONT FAMILIES */
	--font-stack: 'fira code', monospace, sans-serif;
	--highlight-font-family: 'roboto mono', ubuntu, system-ui, blinkmacsystemfont, sans-serif;
	--global-font-family: ubuntu, -apple-system, serif;

	/* FONT WEIGHTS */
	--hairline: 100;
	--thin: 200;
	--light: 300;
	--normal: 400;
	--medium: 500;
	--semibold: 600;
	--heavy: 600;
	--bold: 700;
	--extrabold: 800;
	--superbold: 900;

	/* FONT SIZES */
	--global-font-size: 10px;
	--global-font-line-height: 1.4;
	--article-font-size: clamp(18px, 1.17vw, 24px);

	/* CUSTOM EDITOR FONT SIZES */
	--font-size-small: calc(6px / var(--global-font-size) * 1rem);
	--font-size-regular: calc(12px + 0.698vw);
	--font-size-large: calc(22px + 0.698vw);
	--font-size-larger: calc(32px + 0.698vw);
	--font-size-largest: calc(48px + 0.698vw);

	--font-size-h1: calc(48px + 0.698vw);
	--font-size-h2: calc(32px + 0.698vw);
	--font-size-h3: calc(22px + 0.698vw);
	--font-size-h4: calc(12px + 0.698vw);

	/* link https://www.fluiditype.com */
	--fontsize-xxxs: calc(2px + 0.698vw);
	--lineHeight: calc(var(--fontsize-s) * 1.4);
	--letterSpacing: calc(var(--fontsize-s) * 0.0015);

	--fontsize-xxs: clamp(8px, 0.6vw, 12px);
	--fontsize-xs: clamp(10px, 0.78vw, 16px);
	--fontsize-s: clamp(14px, 1.95vw, 20px);
	--fontsize-m: clamp(18px, 1.17vw, 24px);
	--fontsize-l: clamp(22px, 2.5vw, 38px);
	--fontsize-xl: clamp(30px, 5vw, 64px);
	--fontsize-xxl: clamp(40px, 8vw, 90px);
	--previous-next-font-size: clamp(20px, 3.5vw, 70px);

	--masthead-text-largest: clamp(60px, 6vw, 80px);
	--masthead-text-large: clamp(40px, 5vw, 60px);

	/* --fontsize-xxs: clamp(0.8rem, 2.60vw, 1.10rem);
	--fontsize-xs: clamp(0.98rem, 2.60vw, 1.30rem);
	--fontsize-s: clamp(1.30rem, 2.93vw, 1.56rem);
	--fontsize-m: clamp(1.43rem, 3.58vw, 2.27rem);
	--fontsize-l: clamp(1.63rem, 3.90vw, 2.60rem);
	--fontsize-xl: clamp(1.95rem, 4.55vw, 3.25rem); */

	/*#endregion*/

	/* TAILWIND COLORS */
