	/*#endregion*/

	/*#region*/
	--yellow: var(--yellow-600);
	--red: var(--red-600);
	--green: var(--green-600);
	--blue: var(--blue-600);
	--black: var(--gray-900);
	--grey: var(--gray-500);
	--gray: var(--gray-500);
	--primary: var(--blue-600);
	--orange: var(--orange-600);
	--indigo: var(--indigo-600);
	--secondary: var(--indigo-600);
	--color-theme-primary: var(--blue-600);
	--color-theme-secondary: var(--indigo-600);
	--color-theme-red: var(--red-600);
	--color-theme-green: var(--green-600);
	--color-theme-blue: var(--blue-600);
	--color-theme-yellow: var(--yellow-600);
	--color-theme-black: var(--gray-900);
	--color-theme-grey: var(--gray-500);
	--color-theme-white: var(--foreground);

	/* CUSTOM EDITOR COLORS */
	--color-custom-sun: var(--yellow-200);
	--nav-ul-bg: var(--foreground);
	--color-link: var(--color-theme-primary);
	--color-link-visited: var(--indigo-800);
	--color-link-active: var(--blue-800);

	/* General */
	--body-bg-color: var(--foreground);
	--bg-body-color: var(--foreground);
	--hr-color: var(--gray-400);

	/* TEXT COLOR */
	--text-color-dark-bg: var(--foreground);
	--has-dark-bg: var(--foreground);
	--text-color: var(--gray-800, #515153);
	--global-font-color: var(--text-color);
	--global-text-color: var(--text-color);

	/* END Tailwind Colors */

	--content-width: 60vw; /* initially 45 rem */
	--nav-width: 100vw; /* initially 45 rem */
	--dropdown-symbol-width: 0.7em;
	--h1-letter-spacing: 0.08em;

	/* QUOTES */
	--color-quote-border: var(--gray-800);
	--color-quote-citation: var(--gray-600);
/*#endregion*/
	/* BEZIER TRANSITIONS */
	/*#region*/
	--transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
	--reverse-timing-function: cubic-bezier(0.53, -1.09, 0.44, 1.18);
	--ttf: var(--transition-timing-function);
	--rtf: var(--reverse-timing-function);
	--alternate-bezier-transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
	--bezier-transition: cubic-bezier(0.6, -0.28, 0.735, 0.045);
	--bg-bezier: cubic-bezier(0.21, 1.47, 0.89, 1.51);
	/*#endregion*/

	/* ADMIN VARIABLES */
	/*#region*/
	--admin-theme-primary: var(--color-theme-primary);
	--table-header-color: var(--yellow-600);
	--table-header-bg: var(--admin-theme-primary);
	--admin-font: var(--font-stack);
	--admin-font-size: 1.15rem;
	--admin-row-actions-font-size: 8px;
	--admin-heading-color: var(--global-font-color);
	--admin-bg-color: var(--admin-theme-primary);
	--admin-bg-darker: var(--color-link-visited);
	--admin-bg-lighter: var(--indigo-400);
	--admin-light-links: var(--foreground);
	--admin-dark-links: var(--global-font-color);
	--admin-table-bg: var(--indigo-100);
	--admin-link: var(--admin-theme-primary);
	--admin-link-hover: var(--indigo-900);
	--submenu-width: 180px;
	--paragraph-color: var(--global-font-color);
	--star-rating-size: 1.25rem;
	/*#endregion*/

	/* LOGO */
	/*#region*/
	--jones-svg-fill: var(--blue-600);
	--tagline-svg-fill: var(--gray-500);
	--logo-letter-fill: var(--foreground);
	--logo-circle-fill: var(--gray-300);
	--logo_text_fill: var(--logo-letter-fill);
	--logotype-tagline-display: none;
	--jonessign-text-svg-tagline-fill: var(--gray-500);
	/*#endregion*/

/*#region*/
	/* PRE */
	--pre-bg: var(--blue-700);
	--pre-color: var(--foreground);

	/* Flickity */
	--flickity-button-color: var(--gray-900);

	/* Gradient */
	--admin-gradient: linear-gradient(45deg, var(--indigo-400) 0%, var(--indigo-600) 50%, var(--indigo-800) 100%);
	--wesbos-gradient: linear-gradient(110deg, var(--pink-700), var(--indigo-500) );
	--blue-gradient: linear-gradient(110deg, var(--blue-700), var(--blue-500) );
	--header-gradient: linear-gradient(110deg, var(--blue-700), var(--indigo-600) );

	/* FRONTPAGE PROJECTS */
	--cb-color: var(--gray-900);
	--projects-accent-color: var(--color-theme-primary);
	--check-color: var(--blue-600);
	--box-bg: var(--check-color);
	--dark: var(--gray-900);
	--border-thicknesss: 0.5px;
	--border-type: dashed;
	--border: var(--border-thicknesss) var(--border-type);
	--dashed-border: 1px dashed var(--gray-900);
	--card-bg: #fdfdfd;
	--location-text-color: var(--foreground);
	--more-info-bg: var(--check-color);
	--flickity-button-color: var(--color-theme-primary);

	/* MENU */
	--main-menu-bg: var(--color-theme-primary);
	--sidemenu-width: 220px;
	/* SIDE HAMBURGER MENU */

	--hamburger-menu-background-color-one: var(--color-theme-primary);
	--hamburger-menu-background-color-two: rgba(40, 80, 120, 0.6);
	--burger-div-height: 45px;
	--hamburger-menu-width: 60px;

	/* Shadows */
	--shadow-one: 0 1px 3px rgba(0, 0, 0, 0.12);
	--shadow-two: 0 1px 2px rgba(0, 0, 0, 0.24);
	--section-shadow: 0 8px 19px var(--gray-700);
	--hamburger-menu-box-shadow: var(--shadow-one), var(--shadow-two);
	--inset-shadow: inset 0 0 10px rgba(0, 0, 15, 0.48);
	--btn-box-shadow:
		0 2.8px 2.2px rgba(0, 0, 0, 0.034),
		0 6.7px 5.3px rgba(0, 0, 0, 0.048),
		0 12.5px 10px rgba(0, 0, 0, 0.06),
		0 22.3px 17.9px rgba(0, 0, 0, 0.072),
		0 41.8px 33.4px rgba(0, 0, 0, 0.086),
		0 100px 80px rgba(0, 0, 0, 0.12);
	--contact-form-shadow:
		0 16px 24px 2px rgba(0, 0, 0, 0.14),
		0 6px 30px 5px rgba(0, 0, 0, 0.12),
		0 8px 10px -5px rgba(0, 0, 0, 0.3);
	/*#endregion*/


	/* FOOTER */
	--address-color: var(--gray-900);
	--address-font-size: 1.1rem;
	--animation-delay: 3s;
	--animation-duration: 6s;
	--background-color: #0273b9;
	--footer-text-color: var(--foreground);
	--link-color: #ce606f;
	--map-m-top: 3em;
	--map-max-width: 100%;
	--marker-border-color: var(--marker-color);
	--marker-color: var(--yellow-600);
	--marker-header-bg-color: var(--color-theme-primary);
	--state-fill: var(--color-theme-primary);
	--state-stroke-width: 2;
	--state-stroke: var(--blue-500);
	--subline-color: var(--gray-500);

	/* FORMS */
	--field-focus-text-color: var(--gray-600);

	/* Icons */
	--logo-background: var(--gray-100);
	--logo-foreground: var(--color-theme-blue);

	/* DEV RELATED */
	--dev-border: 2px solid var(--yellow);

	/* DISPLAY or NOT */
	--grid-until-hide: grid;
	--inline-until-hide: inline;
	--block-until-hide: block;
	--flex-until-hide: flex;
	--hide-until-flex: none;
	--hide-until-wide: none;
	--hide-until-block: none;
	--hide-until-grid: none;

	--grid-hide-grid: none;
	--hide-grid-hide: grid;
	--hide-flex-hide: flex;
	--flex-hide-flex: none;
	--hide-block-hide: block;
	--block-hide-block: none;

}

/* stylelint-enable */
@media (prefers-color-scheme: dark ) {

	:root {
		--foreground: #f8f8f8; /* nearly white */
		--background: #111; /* nearly black */

		--text-color: var(--foreground);
		--body-bg-color: var(--blue-900);
		--jones-svg-fill: var(--foreground);
		--tagline-svg-fill: var(--gray-200);
	}
}

/* at 1400 px we get a whole new site layout */
@media screen and ( width >= 1400px ) {

	:root {
		/* Scoll the body? Normally, yes. Widescreens, no */
		--body-overflow: hidden;

		/* font size should match on wide and slim screens */
		--fontsize-xxs: 0.8em;
		--fontsize-xs: 1em;
		--fontsize-s: 1.8em;
		--fontsize-m: clamp(2.2em, 2vw, 3em);
		--fontsize-l: 2.8em;
		--fontsize-xl: 3.9em;
		--fontsize-xxl: 5em;

		--font-size-h1: calc(60px + 0.698vw);
		--font-size-h2: calc(50px + 0.698vw);
		--font-size-h3: calc(34px + 0.698vw);
		--font-size-h4: calc(12px + 0.698vw);

		/* DISPLAY or NOT */
		--hide-on-wide: none;
		--grid-until-hide: none;
		--inline-until-hide: none;
		--block-until-hide: none;
		--flex-until-hide: none;

		--hide-until-flex: flex;
		--hide-until-wide: block;
		--hide-until-block: block;
		--hide-until-grid: grid;

		--grid-hide-grid: grid;
		--hide-grid-hide: none;
		--hide-flex-hide: none;
		--flex-hide-flex: flex;
		--hide-block-hide: none;
		--block-hide-block: block;
	}
}
@media screen and ( width < 768px ) {

	:root {
		/* font size should match on wide and slim screens */
		--fontsize-xxs: clamp(8px, 0.6vw, 12px);
		--fontsize-xs: clamp(10px, 0.78vw, 16px);
		--fontsize-s: clamp(14px, 1.15vw, 22px);
		--fontsize-m: clamp(22px, 2vw, 28px);
		--fontsize-l: clamp(26px, 2.5vw, 38px);
		--fontsize-xl: clamp(30px, 3.9vw, 64px);
		--fontsize-xxl: clamp(40px, 5.5vw, 90px);

		/* DISPLAY or NOT */
		--grid-hide-grid: grid;
		--hide-grid-hide: none;
		--hide-flex-hide: none;
		--flex-hide-flex: flex;
		--hide-block-hide: none;
		--block-hide-block: block;
	}
}
