body,
main,
div#job-requisitions-module {
	margin: auto;
}

body {
	--test-border-color: #ffcc66;
	--test-border-type: solid;
	--test-border: 2px var(--test-border-type) var(--test-border-color);
	--white: #f8f8f8;
	--button-color: var(--white);
	--blue-highlight: rgb(39, 39, 164);
	--blue-highlight: #1a3056; /* Heipro dark navy -- not 40, 80, 120 as in our brand guide */
	--toggle-bkg: var(--blue-highlight);
	--brand-font: 'Roboto Condensed',
	--font-family: var(--brand-font), verdana, monospace;
	--link-hover: #1a3056;
	--button-font-size: 14px;
	--yellow: rgb(255, 198, 0);
}

.top-job-requisitions {
	display: grid;
	place-items: center;
	text-transform: uppercase;
	letter-spacing: 1em;

	& h4 {
		font-size: 1.9ch;
		letter-spacing: 0.3ch;
	}
}

#job-requisitions-module {
	--select-width: 100%;
	display: flex;
	flex-flow: column nowrap;
	align-items: stretch;
	position: relative;
	width: 100%;
	padding-top: 10px;
	margin-top: 22px;

	@media (--wide) {
		--select-width: calc(100%-240px);
		flex-flow: row nowrap;
	}

}

/* ================= Select Element Container & Children Styles ================= */
#job-requisitions-select-element-container {
	--select-width: 100%;
	display: flex;
	flex-flow: column nowrap;
	background-color: #1a3056;
	min-height: 240px;
	width: var(--select-width);
	position: relative;
	padding-bottom: 30px;
	padding-top: 30px;
	margin-bottom: 2.2ch;

	@media (--wide) {
		height: 100vh;
		position: sticky;
		top: 5px;
		left: 0;
	}
}

.job-requisitions-select-element {
	width: 100%;
	display: flex;
	flex-flow: row nowrap;

	@media (--wide ) {
		flex-flow: column nowrap;
		justify-content: flex-start;
	}

	& > .select-cities-top-container {
		display: none;

		@media (--wide) {
			display: none;
		}
	}

	& > .select-cities-bottom-container {
		width: 100%;
		display: flex;
		flex-flow: column nowrap;
		justify-content: center;
		align-items: center;
		margin-top: 16px;
		padding-top: 2vw;

		& .select-cities-bottom-button-container {
			margin: 15px 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
		}
	}
}



/* City names in a job requisitions popover are highlighted when the corresponding input is checked*/
.highlight-this-city {
	color: var(--yellow);
	order: -1;
}


button.select-cities-bottom-button {
	color: var(--button-color);
	border: 2px solid var(--button-color);
	font-size: var(--button-font-size);
	font-weight: 700;
	text-transform: uppercase;
	min-width: 180px;
	transform: scale(0.8);
	position: relative;
	line-height: 1.1;
	padding: 10px;
	letter-spacing: 2px;
	background: var(--toggle-bkg);
	transition: all 1.9s ease;

	&:disabled {
		background: transparent;
		border-color: transparent;
	}

	& > div {
		display: flex;
	}
}

.cities-select-container {
	width: 94vw;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	max-height: 240px;
	padding-left: 3vw;

	@media (--phone) {
		width: 100%;
	}

	@media (--narrow) {
		width: 100%;
	}

	@media (--wide) {
		padding-left: 14px;
		width: 100%;
		max-height: unset;
	}

}

/* Single selector checkbox container - a span that contains input and label */
.city-select-container {
	position: relative;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-content: flex-start;
	align-items: flex-end;
	overflow: hidden;
	color: #efefef;
	min-height: 28px;
	border-color: rgba(0 0 0/0.04);
	border-left: 1px solid rgba(0 0 0/0.04);
	margin-left: 2px;
	width: calc(94vw/4.5);
	padding-left: 3px;

	@media (--phone) {
		width: 44vw;
	}

	@media (--wide) {
		width: 100%;
		min-height: 30px;
	}

	/* slides in behind the container when the input is checked */
	&::before {
		position: absolute;
		top: 0;
		left: 0;
		content: '';
		height: 100%;
		width: 80%;
		min-width: 120px;
		min-height: 100%;
		background: var(--frosty);
		border-bottom: 2px solid rgba(0, 0, 0, 0.15);
		border-left: 2px solid rgba(255, 255, 255, 0.25);
		transform: translateX(-102%);
		transition: all 0.6s cubic-bezier(0.1, 1.15, 0.84, 1.04);
	}

	&.inputChecked {
		&::before {
			top: -2px;
			transform: translateX(0);
			transition: all 0.6s cubic-bezier(0.1, 1.15, 0.84, 1.04);
		}
	}

	& label.city-select-label {
		font-weight: 500;
		letter-spacing: 1px;
		display: unset;
		margin-bottom: unset;
		position: absolute;
		left: 26px;
		bottom: 1px;
		font-size: 16px;


		@media (--wide) {
			font-size: 18px;
		}
	}

	& > input {
		position: absolute;
		left: 6px;
		bottom: 4px;
		accent-color: #ffc600;
		margin: 0;
		align-self: center;

		&:checked + label {
			color: #ffc600;
		}

	}
}



[data-jobs="2"],
[data-jobs="1"],
[hidden] {
	display: none;
}


